.item {
    
   display: flex;
       border: 1px solid #ccc;
    border-radius: 2px;
    box-shadow: 5px 5px 5px #ccc;
    margin-bottom: 10px;
    width: 200px;
    align-items: center;
    margin-left: 0;
    margin-right: 0;
    padding: 0;

}

.friendlist {
   display: flex;
   flex-direction: column;
}

.avatar{
    padding: 10px;
}

.status {
    background-color: green;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    margin-left: 10px;
}

.statusof {
    background-color: red;
        width: 15px;
        height: 15px;
        border-radius: 50%;
        margin-left: 10px;
}